.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 30px 0;
  width: 100%;

  &__container {
    @include flex-all-sb;

    @include high-tablet {
      align-items: center;
      justify-content: initial;
    }
  }

  &__phone {
    @include high-tablet {
      position: relative;
      margin-right: 20px;
      margin-left: auto;
      border-radius: 10px;
      width: 44px;
      height: 44px;
      font-size: 0;

    }
  }
}
